export default {
  props: ['activeMark'],
  data() {
    return {
      codemirror: null,
      codeMirrorDoc: null,
      selectedText: '',
      startPos: '',
      endPos: '',
      markList: [],
      quickMarkVis: false,
      quickMarkX: 0,
      quickMarkY: 0,
      showFlag: true,
      fontSize: '14px',
      fontSizeList: [
        '14px',
        '16px',
        '18px',
        '20px',
        '24px',
        '26px',
        '28px',
        '30px',
        '32px',
        '34px',
        '36px',
      ],
      markListVisible: false,
      composeLetters: '',
    };
  },
  mounted() {
    this.codemirror = this.$refs.mycode.codemirror;
    this.codeMirrorDoc = this.codemirror.getDoc();
    this.codemirror.getWrapperElement().style.lineHeight = 2;
    this.codemirror.on('cursorActivity', (cm) => {
      //拖动鼠标开始位置结束位置，支持正反选
      this.selectedText = cm.getSelection();
      this.startPos = cm.getCursor('start');
      this.endPos = cm.getCursor('end');
      document.body.removeEventListener('keyup', this.keyBoardMarkText);
      document.body.addEventListener('keyup', this.keyBoardMarkText);
    });
    if (this.$refs.quickMark) {
      document.body.appendChild(this.$refs.quickMark);
      document.body.addEventListener('click', () => {
        this.selectedText = '';
        this.quickMarkVis = false;
        this.markListVisible = false;
        this.activeKey = '';
      });
      document.querySelectorAll('.vue-codemirror').forEach((item) => {
        item.addEventListener('mouseup', (e) => {
          if (this.selectedText) {
            this.quickMarkX = e.pageX;
            this.quickMarkY = e.pageY;
            this.quickMarkVis = true;
          } else {
            this.quickMarkVis = false;
            this.markListVisible = false;
            this.activeKey = '';
          }
        });
        item.addEventListener('click', (e) => {
          e.stopPropagation();
        });
      });
    }
  },
  methods: {
    // 将位置转换为数字
    transformPosToIndex(pos) {
      return this.codeMirrorDoc.indexFromPos(pos);
    },
    // 将数字转换为位置
    transformIndexToPos(index) {
      return this.codeMirrorDoc.posFromIndex(index);
    },
    // 清除选中区域
    clearSelection() {
      this.selectedText = '';
      return this.codeMirrorDoc.undoSelection();
    },
    // 清除所有的文字标记
    clearAllMarkText() {
      this.codeMirrorDoc.getAllMarks().forEach((item) => {
        item.clear();
      });
    },
    // 标记文字
    markText() {
      this.onceMarkCode = this.selectedText;
      this.clearSelection();
      this.clearAllMarkText();
      this.markList.forEach((item) => {
        const start = this.transformIndexToPos(item.start_pos);
        const end = this.transformIndexToPos(item.end_pos);
        this.codemirror.markText(start, end, {
          css: 'background:' + item.bg_color + ';color:' + item.txt_color,
        });
      });
      this.quickMarkVis = false;
      this.markListVisible = false;
    },
    // 清除选中区域的文字标记
    clearMarkText() {
      const startIndex = this.transformPosToIndex(this.startPos);
      const endIndex = this.transformPosToIndex(this.endPos);
      const newMarkList = this.markList.filter(
        (item) => !(startIndex <= item.start_pos && item.end_pos <= endIndex)
      );
      this.markList = newMarkList;
      this.markText();
    },
    // 点击按钮进行标记文字
    doMarkText(subCategory, e) {
      if (e) {
        e.stopPropagation();
      }
      this.markList.push({
        //按约定组织好集合的结构
        entity_mention: this.selectedText,
        start_pos: this.transformPosToIndex(this.startPos),
        end_pos: this.transformPosToIndex(this.endPos),
        entity_type: subCategory.labelName,
        result_state: 2,
        result_type: 1,
        // result_state_note : _this.result_state_id2des_dict[2],
        // result_type_note : _this.result_type_id2des_dict[1],
        bg_color: subCategory.bg_color,
        txt_color: subCategory.txt_color,
      });
      this.markText();
      this.$emit('addMarkItem', this.markList);
    },
    // 点击编辑按钮显示标记按钮列表
    handleEditClick(e) {
      e.stopPropagation();
      this.markListVisible = true;
    },
    // 改变字体大小
    changeFontSize(newVal) {
      this.codemirror.getWrapperElement().style.fontSize = newVal;
      this.codemirror.refresh();
    },
    keyBoardMarkText(e) {
      if (e.key.toString().length === 1 && /[a-zA-Z]/.test(e.key)) {
        this.composeLetters += e.key;
      }
      const activeMarkItem = this.markItemList.find(
        (item) => item.composeLetters === this.composeLetters
      );
      if (activeMarkItem) {
        this.composeLetters = '';
        this.doMarkText(activeMarkItem);
        return;
      }
      if (
        this.composeLetters.length ===
        this.markItemList[0].composeLetters.length
      ) {
        this.composeLetters = '';
      }
    },
  },
  watch: {
    quickMarkX() {
      this.$refs.quickMark.style.left = this.quickMarkX + 'px';
      this.$refs.quickMark.style.top = this.quickMarkY + 'px';
    },
    activeMark(newVal) {
      const start = this.transformIndexToPos(newVal.start_pos);
      const end = this.transformIndexToPos(newVal.end_pos);
      this.codeMirrorDoc.setSelection(start, end);
    },
  },
};
