  <div class="high-query-container">
    <div id="ruleSearchDiv" class="advSearchDiv"></div>
    <el-button @click="handleSubmit" type="primary" style="margin-top: 20px"
      >查询</el-button
    >
    <el-button @click="handleCancel" type="primary" style="margin-top: 20px"
      >取消</el-button
    >
  </div>
</template>

<script>
const columnData = { connect: '', field: '', operator: '' };
let advSearch = null;
export default {
  data() {
    return {
      highQueryList: [{ connect: '', field: '', operator: '' }],
    };
  },
  mounted() {
    var searchJson = ''; //初始化数据
    var advSelectJson = [
      { value: 'a.NEW_NAME', name: '转换后检查项目名称' },
      { value: 'a.NEW_KIND_NAME', name: '转换后设备类型名称' },
      { value: 'a.TRANSFORMED_GNAME', name: '转换后规范元素名称' },
      { value: 'a.TRANSFORMED_GVALUE', name: '转化后规范值' },
    ];
    advSearch = new this.$AdvanceSearch(
      'ruleSearchDiv',
      searchJson,
      advSelectJson,
      false
    ); //false初始化时不显示内容
    advSearch.initEvent(); // 事件绑定初始化
    advSearch.getjsonData(); //查询条件json数据
  },
  methods: {
    handleSubmit() {
      const queryJson = advSearch.getjsonData();
      this.$emit('querySubmit', queryJson);
    },
    handleCancel() {
      this.$emit('handleCancel');
    },
    addColumn() {
      this.highQueryList.push(JSON.parse(JSON.stringify(columnData)));
    },
  },
};
</script>

