import Vue from 'vue';
import VueRouter from 'vue-router';
import Mark from 'pages/Mark';
import TaskMark from 'pages/Mark/TaskMark';
import FileAdmin from 'pages/FileAdmin';
import TaskAdmin from 'pages/FileAdmin/TaskAdmin';
import FileUpload from 'pages/FileAdmin/FileUpload';
import FileDownload from 'pages/FileAdmin/FileDownload';

Vue.use(VueRouter);

export default new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/fileAdmin/fileDownload',
    },
    {
      path: '/mark',
      component: Mark,
      children: [
        {
          path: 'taskMark',
          component: TaskMark,
        },
      ],
    },
    {
      path: '/fileAdmin',
      component: FileAdmin,
      children: [
        {
          path: 'taskAdmin',
          component: TaskAdmin,
        },
        {
          path: 'fileUpload',
          component: FileUpload,
        },
        {
          path: 'fileDownload',
          component: FileDownload,
        },
      ],
    },
  ],
});
