<template>
  <div class="upload">
    <div class="process">
      <div class="process-bar"></div>
    </div>
    <div class="file-upload-action">
      <el-input
        placeholder="请选择您想上传的文件"
        class="file-list"
        v-model="fileNames"
        readonly
      />
      <span class="action gray" @click="removeAllFiles">全部移除</span>
      <span class="action gray">上传</span>
      <el-upload
        class="upload-demo"
        action="https://jsonplaceholder.typicode.com/posts/"
        multiple
        :show-file-list="false"
        :auto-upload="false"
        :on-change="handleUploadFileChange"
        ref="fileUpload"
        accept="application/vnd.ms-excel"
      >
        <span class="action primary">选择</span></el-upload
      >
    </div>
    <el-table :data="fileList" border>
      <el-table-column type="selection" width="55" />
      <el-table-column prop="name" label="原始文件名" sortable />
      <el-table-column prop="name2" label="样本集名称" sortable />
      <el-table-column prop="name3" label="文件大小" sortable />
      <el-table-column prop="name4" label="文件类型" sortable />
    </el-table>
    <el-pagination
      prev-text="上一页"
      next-text="下一页"
      layout="prev, pager, next"
      background
      :total="100"
    />
    <div class="submit-btn">
      <el-button type="danger">删除选择的文件</el-button>
      <el-button type="primary">确定选择文件</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    fileList: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      fileNames: '',
    };
  },
  methods: {
    handleUploadFileChange(file, fileList) {
      console.log(file, fileList);
      this.fileNames = fileList.map((item) => item.name).join(';');
    },
    removeAllFiles() {
      this.$refs.fileUpload.clearFiles();
      this.fileNames = '';
    },
  },
};
</script>

