import $ from 'jquery';
import jQuery from 'jquery';
import { checkS } from './check';

export const AdvanceSearch = function (
  containerDiv,
  dataJson,
  selectJson,
  showHidden
) {
  var _this = this;
  _this.containerDiv = containerDiv;
  _this.dataJson = dataJson;
  _this.selectJson = selectJson;
  _this.isValidator = showHidden;
  _this.initialize();
  _this.firstSelectLength = '';

  _this.showHidden(showHidden);
};
AdvanceSearch.prototype = {
  containerDiv: null,
  dataJson: null,
  selectJson: null,
  // showHidden: null,
  firstSelectLength: null,
  initialize: function () {
    var self = this;

    var searHtml;

    if (
      typeof self.dataJson != 'undefined' &&
      self.dataJson != null &&
      self.dataJson != ''
    ) {
      var json_arr = JSON.parse(self.dataJson);
      $.each(json_arr, function (index, json) {
        if (index == 0) {
          searHtml = self.firstRowInit(json);
        } else {
          searHtml += self.addRow(json);
        }
      });
    } else {
      searHtml = self.firstRowInit();
    }
    $('#' + self.containerDiv).append(searHtml);
  },
  /**
   * 初始化第一行
   */
  firstRowInit: function (rowDataJson) {
    var self = this;
    rowDataJson = self.commonRowJson(rowDataJson);
    var initHtml =
      '<table class="el-table" id=' +
      self.containerDiv +
      'list-table>' +
      self.tableTitle() +
      '<tr class="adv-row">' +
      self.blankDiv() +
      self.leftBracketsDiv(rowDataJson.leftbrackets) +
      self.selectDiv(rowDataJson.searchField) +
      self.conditionDiv(rowDataJson.searchSymbol) +
      self.contentDiv(rowDataJson.searchValue) +
      self.rigthBracketsDiv(rowDataJson.rightbrackets) +
      self.addNewRowDiv() +
      '</tr></table>';
    return initHtml;
  },
  /**
   * 添加条件，>1的其他行
   */
  addRow: function (rowDataJson) {
    var self = this;
    rowDataJson = self.commonRowJson(rowDataJson);
    var addHtml =
      '<tr class="adv-row">' +
      self.logicalDiv(rowDataJson.searchLogical) +
      self.leftBracketsDiv(rowDataJson.leftbrackets) +
      self.selectDiv(rowDataJson.searchField) +
      self.conditionDiv(rowDataJson.searchSymbol) +
      self.contentDiv(rowDataJson.searchValue) +
      self.rigthBracketsDiv(rowDataJson.rightbrackets) +
      self.deleteDiv() +
      '</tr>';
    return addHtml;
  },

  getjsonData: function () {
    var self = this;
    var json_arr = new Array();
    var isNoProblem = false;
    if (self.checkExpression()) {
      $('#' + self.containerDiv + ' .adv-row').each(function (i) {
        var objBContent = $(this).find('.adv-value'); //取得内容对象
        var objCondition = $(this).find('.adv-condition'); //取得下拉运算符选择对象
        // var logical = $(this).find('.adv-logical'); //取得条件运算符对象
        var condition = objCondition.val();
        var select_type = objCondition.attr('select_type'); //内容类别
        // var select_size = objCondition.attr('select_size');
        var adv_value = ''; //参数值
        if (objCondition.val() == 'BETWEEN') {
          adv_value =
            "'" +
            $(this).find('.adv-value1').val() +
            "' and '" +
            $(this).find('.adv-value2').val() +
            "'";
        } else if (objCondition.val() == 'IS_NULL') {
          adv_value = 'NULL';
          condition = 'IS';
        } else if (objCondition.val() == 'IS_NOTNULL') {
          adv_value = 'NOT NULL';
          condition = 'IS';
        } else {
          adv_value = $(this).find('.adv-value').val();
        }

        switch (objCondition.val().toLowerCase()) {
          case '': // 不查询时不做任何事情
            break;
          case 'like': // 查询条件为like时
            if (
              self.checkDataLike(
                objBContent,
                '',
                self.emendationType(select_type)
              )
            ) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
          case 'in': // 查询条件为in时
            if (
              self.checkDataIn(
                objBContent,
                '',
                self.emendationType(select_type)
              )
            ) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
          case 'not_in': // 查询条件为not in时
            if (
              self.checkDataIn(
                objBContent,
                '',
                self.emendationType(select_type)
              )
            ) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
          case 'between': // 查询条件为between时
            objBContent = $(this).find('.adv-value1');
            var objEContent = $(this).find('.adv-value2'); // between查询时才会出现的第二个条件内容对象
            var objArray = new Array(objBContent, objEContent);
            if (
              self.checkDataBetween(
                objArray,
                '',
                self.emendationType(select_type)
              )
            ) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
          case 'is': // 查询条件为is时
            if (self.checkDataIs(objBContent, '', 'checknull')) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
          default:
            // 查询条件为其他时
            if (
              self.checkDataNormal(
                objBContent,
                '',
                self.emendationType(select_type)
              )
            ) {
              isNoProblem = true;
            } else isNoProblem = false;
            break;
        }
        if (isNoProblem) {
          var searchJson = {};
          searchJson.searchValue = adv_value; //参数值
          searchJson.searchLogical = $(this).find('.adv-logical').val(); //或与非
          searchJson.searchField = $(this).find('.adv-select-field').val(); //字段参数名
          searchJson.searchSymbol = condition; //运算符
          searchJson.leftbrackets = $(this).find('.adv-leftbrackets').val(); //左括号
          searchJson.rightbrackets = $(this).find('.adv-rightbrackets').val(); //右括号
          var adv_select_field = $(this)
            .find('.adv-select-field')
            .find('option:selected');
          searchJson.searchType = adv_select_field.attr('select_type');
          searchJson.searchNumber = i;
          json_arr.push(searchJson);
        }
      });
    }
    return JSON.stringify(json_arr);
  },
  // 数据校正(like查询专用)
  checkDataLike: function (objTemp, strText, action) {
    var strTemp = objTemp.val();
    if (strTemp == '') return true;
    return checkS(objTemp, strTemp, strText, action);
  },
  // 数据校正(in或not in查询专用)
  checkDataIn: function (objTemp, strText, action) {
    var strTemp = objTemp.val();
    var temps = strTemp.split(',');
    var boolOK = true;
    for (var i = 0; i < temps.length; i++) {
      if (temps[i] != '') boolOK = checkS(objTemp, temps[i], strText, action);
      if (boolOK == false) break;
    }
    return boolOK;
  },
  // 数据校正(between查询专用)
  checkDataBetween: function (objTemp, strText, action) {
    var strTemp = new Array(objTemp[0].val(), objTemp[1].val());
    var boolOK = true;
    for (var i = 0; i < objTemp.length; i++) {
      if (strTemp[i] != '')
        boolOK = checkS(objTemp[i], strTemp[i], strText, action);
      if (boolOK == false) break;
    }
    return boolOK;
  },
  // 数据校正(is查询时用)
  checkDataIs: function (objTemp, strText, action) {
    var strTemp = objTemp.val();
    return checkS(objTemp, strTemp, strText, action);
  },
  // 数据校正(一般查询时用)
  checkDataNormal: function (objTemp, strText, action) {
    var strTemp = objTemp.val();
    if (strTemp == '') return true;
    return checkS(objTemp, strTemp, strText, action);
  },
  //语法校验
  checkExpression: function () {
    var self = this;
    //左括号值
    var strLParenthesis = '';
    //右括号值
    var strRParenthesis = '';
    //两边括号值
    var strAllParenthesis = '';
    //校验字段是否为空； 去除所有括号
    $('#' + self.containerDiv + ' .adv-row').each(function () {
      var objField = $(this).find('.adv-select-field'); // 取得字段选择控件对象
      var objCondition = $(this).find('.adv-condition'); // 取得条件运算符选择控件对象
      var objLParenthesisPanel = $(this).find('.adv-leftbrackets'); // 取得左括号面板对象
      var objRParenthesisPanel = $(this).find('.adv-rightbrackets'); // 取得右括号面板对象
      var rParenthes_val = '';
      if (typeof objRParenthesisPanel.val() == 'undefined') {
        rParenthes_val = '';
      } else {
        rParenthes_val = objRParenthesisPanel.val();
      }
      if (objField.val() == '') {
        // 字段是否为空
        alert('所选的条件中存在不明确的字段名，请先选择一个有效字段名！');
        objField.focus();
        return false;
      }
      if (
        objCondition.val() == '' &&
        (objLParenthesisPanel.val() != '' || rParenthes_val != '')
      ) {
        alert('不查询的字段两边不能加括号，请将不合法的括号删除！');
        objField.focus();
        return false;
      }
      strLParenthesis += objLParenthesisPanel.val();
      strRParenthesis += rParenthes_val;
      strAllParenthesis += objLParenthesisPanel.val() + rParenthes_val;
    });
    // "("和")"数量是否相等 左右括号是否对等
    if (strLParenthesis.length != strRParenthesis.length) {
      alert('"("与")"的数量不对应，请检查！');
      return false;
    }

    //  "("和")"的位置是否正确设置 循环遍历括号，并记录括号数量
    var arrParenthesis = strAllParenthesis.split('');
    var lCount = 0;
    var rCount = 0;
    for (var i = 0; i < arrParenthesis.length; i++) {
      if (arrParenthesis[i] == '(') {
        lCount += 1;
      } else if (arrParenthesis[i] == ')') {
        rCount += 1;
      }
      if (lCount < rCount) {
        alert('"("与")"存在错误设置，请检查！');
        return false;
      }
    }

    return true;
  },
  // 根据类型返回相应校正的项目
  emendationType: function (strType) {
    if (strType == 'number') {
      return 'checknumber';
    } else if (strType == 'date' || strType == 'datetime') {
      return '';
    }
    return '';
  },
  initEvent: function () {
    var self = this;

    $('#' + self.containerDiv).on('click', '.adv-row .adv-minus', function () {
      var bracket = $(this).parent().next();
      var bracket_val = bracket.val();
      bracket_val = bracket_val.substring(0, bracket_val.length - 1);
      bracket.val(bracket_val);
    });

    $('#' + self.containerDiv).on('click', '.adv-row .adv-plus', function () {
      var bracket_val = $(this).attr('bracket-val');
      var bracket = $(this).parent().prev();
      bracket.val(bracket.val() + bracket_val);
    });

    $('#' + self.containerDiv).on(
      'click',
      '.adv-row .adv-delete-btn',
      function () {
        $(this).parent().parent().parent().remove();
      }
    );

    $('#' + self.containerDiv + ' .adv-add-btn').click(function () {
      var addHtml = self.addRow(null, self.selectJson);
      $('#' + self.containerDiv + 'list-table').append(addHtml);
    });
    $('#' + self.containerDiv).on('change', '.adv-condition', function () {
      var select = $(this).val();
      var select_next = $(this).parent().next();
      var select_prev = $(this).parent().prev();
      var adv_value = select_next.find('.adv-value').val();
      var select_size = select_prev.find('.adv-select-field');
      var option_size_val = select_size
        .find('option:selected')
        .attr('select_size');
      var adv_contentHtml = '';
      if (select == 'BETWEEN') {
        adv_contentHtml =
          "<input type='text' placeholder='请输入内容一' class='form-control form-control-sm adv-value1' style='width: 47%; float: left;' value='" +
          self.commonIsEmpty(adv_value) +
          "' maxlength='" +
          self.commonIsEmpty(option_size_val) +
          "'>" +
          "<span class='input-group' style='color: #d2d6de;'>—</span>" +
          "<input type='text' placeholder='请输入内容二' class='form-control form-control-sm adv-value2' style='width: 48.5%; float:right;' value='' maxlength='" +
          self.commonIsEmpty(option_size_val) +
          "'>";
      } else if (select == 'IS_NULL') {
        adv_contentHtml =
          "<input type='text' class='form-control form-control-sm adv-value' style='width: 100%;' placeholder='null' readonly='true'>";
      } else if (select == 'IS_NOTNULL') {
        adv_contentHtml =
          "<input type='text' class='form-control form-control-sm adv-value' style='width: 100%;' placeholder='not null' readonly='true'>";
      } else {
        if (select == 'IN' || select == 'NOT_IN') {
          option_size_val = '2147483647';
        }
        adv_contentHtml =
          "<input type='text' placeholder='请输入内容' class='form-control form-control-sm adv-value' style='width: 100%;' value='" +
          self.commonIsEmpty(adv_value) +
          "' maxlength='" +
          self.commonIsEmpty(option_size_val) +
          "'>";
      }
      select_next.html(adv_contentHtml);
    });

    $('#' + self.containerDiv).on('change', '.adv-select-field', function () {
      var self = $(this);
      var select_size = self.find('option:selected').attr('select_size');
      var content_div = $(this).parent().next().next(); //内容对象
      var selectCondition_div = $(this).parent().next(); //运算符对象
      var conditionVal = selectCondition_div.find('option:selected').val();
      content_div.find('.adv-value').attr('maxlength', select_size);
      if (conditionVal == 'BETWEEN') {
        content_div.find('.adv-value1').attr('maxlength', select_size);
        content_div.find('.adv-value2').attr('maxlength', select_size);
      } else {
        if (conditionVal == 'IN' || conditionVal == 'NOT_IN') {
          select_size = '2147483647';
        }
        content_div.find('.adv-value').attr('maxlength', select_size);
      }
    });

    /**
     * like 添加左中右按钮
     */
    /*"<div>"
          +"<label class='radio-inline adv-like'><input type='radio' name='likeLeftRigth' value='like'>左右</label>"
          +"<label class='radio-inline adv-like'><input type='radio' name='likeLeftRigth' value='likeLeft'>左</label>"
        +"<label class='radio-inline adv-like'><input type='radio' name='likeLeftRigth' value='likeRigth'>右</label>"
         * $("#"+self.containerDiv).on( "click", ".adv-like input", function() {
          var val_btn = $(this).val();
          var advValue = $(this).parent().nextAll(".adv-value").val();
          advValue = self.trim(advValue, 'left')
          advValue = self.trim(advValue, 'right')
          if(val_btn == "like"){
            advValue = "%"+advValue+"%";
          }else if(val_btn == "likeLeft"){
            advValue = "%"+advValue;
          }else{
            advValue = advValue+"%";
          }
          $(this).parent().nextAll(".adv-value").val(advValue);
        });*/
  },

  tableTitle() {
    return (
      "<tr style='height: 50px'><td style='text-align: center'><div class='input-group' style='width: 80px'>连接</div></td><td style='text-align: center'><div class='input-group'>左括号</div></td>" +
      "<td style='text-align: center'><div class='input-group'>字段</div></td><td style='text-align: center'><div class='input-group'>运算符</div></td><td style='text-align: center'><div class='input-group'>内容</div></td>" +
      "<td style='text-align: center'><div class='input-group'>右括号</div></td><td style='text-align: center'><div class='input-group'>操作</div></td></tr>"
    );
  },

  /**
   * 空白div
   */
  blankDiv: function () {
    var blankHtml =
      "<td><div class='input-group' style='width: 80px'>&nbsp;</div></td>";
    return blankHtml;
  },
  /**
   * 左括号添加&删除
   */
  leftBracketsDiv: function (currVal) {
    var self = this;
    var bracketsHtml =
      "<td><div class='input-group'>" +
      "<span class='input-group-btn'>" +
      "   <button class='btn btn-default btn-sm adv-minus' type='button'>-</button>" +
      '</span> ' +
      "<input type = 'text' class = 'form-control form-control-sm adv-leftbrackets' style = 'width: 60px;' value = '" +
      self.commonIsEmpty(currVal) +
      "' >" +
      "<span class='input-group-btn'>" +
      "   <button class='btn btn-default btn-sm adv-plus' bracket-val='(' type='button'>+</button>" +
      '</span> ' +
      '</div></td>';
    return bracketsHtml;
  },

  /**
   * 右括号 添加&删除
   */
  rigthBracketsDiv: function (currVal) {
    var self = this;
    var backetsHtml =
      "<td><div class='input-group'>" +
      "<span class='input-group-btn'>" +
      " <button class='btn btn-default btn-sm adv-minus' type='button'>-</button>" +
      '</span>' +
      "<input type='text' class='form-control form-control-sm adv-rightbrackets' style='width: 60px;' value='" +
      self.commonIsEmpty(currVal) +
      "'>" +
      "<span class='input-group-btn'>" +
      "<button class='btn btn-default btn-sm adv-plus' bracket-val=')' type='button' >+</button>" +
      '</span>' +
      '</div></td>';
    return backetsHtml;
  },
  /**
   * 下拉选择查询字段
   */
  selectDiv: function (currVal) {
    var self = this;
    var selectHtml =
      "<td><div class='input-group'>" +
      "<select class='form-control form-control-sm adv-select-field'>" +
      self.fieldOption(self.selectJson, self.commonIsEmpty(currVal)) +
      '</select>' +
      '</div></td>';
    return selectHtml;
  },
  /**
   * 查询内容
   */
  contentDiv: function (currVal) {
    var self = this;
    var contentHtml =
      "<td><div class='input-group'>" +
      "<input type='text' placeholder='请输入内容' class='form-control form-control-sm adv-value' style='width: 100%;' value='" +
      self.commonIsEmpty(currVal) +
      "' maxlength='" +
      self.commonIsEmpty(self.firstSelectLength) +
      "'>" +
      '</div></td>';
    return contentHtml;
  },
  /**
   * 查询条件
   */
  conditionDiv: function (currVal) {
    var self = this;
    var optionJson = [
      { value: '=', name: '等于' },
      { value: '<', name: '小于' },
      { value: '<=', name: '小于等于' },
      { value: '">"', name: '大于' },
      { value: '">="', name: '大于等于' },
      { value: '"<>"', name: '不等于' },
      { value: 'IN', name: '属于' },
      { value: 'NOT_IN', name: '不属于' },
      { value: 'BETWEEN', name: '介于' },
      { value: 'IS_NULL', name: '为空' },
      { value: 'IS_NOTNULL', name: '不为空' },
      { value: 'LIKE', name: '包含' },
      { value: 'NOT_LIKE', name: '不包含' },
    ];

    var conditionHtml =
      "<td><div class='input-group'>" +
      "<select class='form-control form-control-sm adv-condition'>" +
      self.commonOption(optionJson, self.commonIsEmpty(currVal)) +
      '</select>' +
      '</div></td>';
    return conditionHtml;
  },
  /**
   * 或与非
   */
  logicalDiv: function (currVal) {
    var self = this;
    var optionJson = [
      { value: 'and', name: 'AND' },
      { value: 'or', name: 'OR' },
    ];
    var logicalHtml =
      "<td><div class='input-group' >" +
      "<select name='logical' class='form-control form-control-sm adv-logical' >" +
      self.commonOption(optionJson, self.commonIsEmpty(currVal)) +
      '</select>' +
      '</div></td>';
    return logicalHtml;
  },
  /**
   * 运算符select下拉生成
   * @param optionJson
   * @param currVal
   * @returns {string}
   */
  fieldOption: function (optionJson, currVal) {
    var self = this;
    var opentionHtml = '';
    if (optionJson.length > 1) {
      $.each(optionJson, function (i, item) {
        if (
          typeof currVal != 'undefined' &&
          currVal != null &&
          currVal != '' &&
          currVal == item.value
        ) {
          self.firstSelectLength = item.size;
          opentionHtml +=
            '<option value=' +
            item.value +
            ' select_type="' +
            self.commonIsEmpty(item.type) +
            '" select_size="' +
            self.commonIsEmpty(item.size) +
            '" selected>' +
            item.name +
            '</option>';
        } else {
          if (i == 0) {
            self.firstSelectLength = item.size;
          }
          opentionHtml +=
            '<option value=' +
            item.value +
            ' select_type="' +
            self.commonIsEmpty(item.type) +
            '" select_size="' +
            self.commonIsEmpty(item.size) +
            '" >' +
            item.name +
            '</option>';
        }
      });
    }
    return opentionHtml;
  },
  /**
   * 公共select下拉框生成
   */
  commonOption: function (optionJson, currVal) {
    var opentionHtml = '';
    if (optionJson.length > 1) {
      $.each(optionJson, function (i, item) {
        if (
          typeof currVal != 'undefined' &&
          currVal != null &&
          currVal != '' &&
          currVal == item.value
        ) {
          opentionHtml +=
            '<option value=' +
            item.value +
            ' selected>' +
            item.name +
            '</option>';
        } else {
          opentionHtml +=
            '<option value=' + item.value + '>' + item.name + '</option>';
        }
      });
    }
    return opentionHtml;
  },
  //公共判断是否为空处理
  commonIsEmpty: function (currVal) {
    if (typeof currVal == 'undefined' || currVal == null || currVal == '') {
      currVal = '';
    }
    return currVal;
  },
  commonRowJson: function (rowDateJson) {
    if (jQuery.isEmptyObject(rowDateJson)) {
      rowDateJson =
        '[{"searchLogical":"","searchField":"","searchValue":"","searchSymbol":"","leftbrackets":"","rightbrackets":"","searchNumber":0}]';
    }
    return rowDateJson;
  },
  /**
   * 添加按钮
   */
  addNewRowDiv: function () {
    var addNewRowHtml =
      "<td><div class='input-group adv-add-btn' >" +
      "<a href='javascript:void(0);' style='margin-left: 10px;'>" +
      "添加<i class='glyphicon glyphicon-plus'></i>" +
      '</a>' +
      '</div></td>';
    return addNewRowHtml;
  },
  /**
   * 删除查询条件
   */
  deleteDiv: function () {
    var deleteHtml =
      "<td><div class='input-group'>" +
      "   <span class='input-group-btn'>" +
      "       <button type='button' class='btn btn-danger btn-sm adv-delete-btn' style='display:inline;'>删除</button>" +
      '   </span>' +
      '</div></td>';
    return deleteHtml;
  },
  showHidden: function (show) {
    var self = this;
    if (show) {
      $('#' + self.containerDiv).hide();
    } else {
      $('#' + self.containerDiv).show();
    }
  },
  trim: function (val, type) {
    if (type == 'left') {
      var leftval = val.substring(0, 1);
      if (leftval == '%') {
        val = val.substring(1, val.length);
      }
    } else if (type == 'right') {
      var rightval = val.substring(val.length - 1, val.length);
      if (rightval == '%') {
        val = val.substring(0, val.length - 1);
      }
    }
    return val;
  },
};
