export default {
  data() {
    return {
      codemirror2: null,
      codeMirrorDoc2: null,
      selectedText2: '',
      startPos2: '',
      endPos2: '',
      twiceMarkList: [],
    };
  },
  mounted() {
    this.codemirror2 = this.$refs.mycode2.codemirror;
    this.codeMirrorDoc2 = this.codemirror2.getDoc();
    this.codemirror2.on('cursorActivity', (cm) => {
      //拖动鼠标开始位置结束位置，支持正反选
      this.selectedText2 = cm.getSelection();
      this.startPos2 = cm.getCursor('start');
      this.endPos2 = cm.getCursor('end');
    });
  },
  methods: {
    // 将位置转换为数字
    transformPosToIndex2(pos) {
      return this.codeMirrorDoc2.indexFromPos(pos);
    },
    // 将数字转换为位置
    transformIndexToPos2(index) {
      return this.codeMirrorDoc2.posFromIndex(index);
    },
    // 清除选中区域
    clearSelection2() {
      this.selectedText2 = '';
      return this.codeMirrorDoc2.undoSelection();
    },
    // 清除所有的文字标记
    clearAllMarkText2() {
      this.codeMirrorDoc2.getAllMarks().forEach((item) => {
        item.clear();
      });
    },
    // 标记文字
    markText2() {
      this.clearSelection2();
      this.clearAllMarkText2();
      this.twiceMarkList.forEach((item) => {
        const start = this.transformIndexToPos2(item.start_pos);
        const end = this.transformIndexToPos2(item.end_pos);
        this.codemirror2.markText(start, end, {
          css: 'background:' + item.bg_color + ';color:' + item.txt_color,
        });
      });
    },
    twiceMarkText(item) {
      this.twiceMarkList.push({
        //按约定组织好集合的结构
        entity_mention: this.selectedText2,
        start_pos: this.transformPosToIndex2(this.startPos2),
        end_pos: this.transformPosToIndex2(this.endPos2),
        entity_type: item.value_type,
        result_state: 2,
        result_type: 1,
        // result_state_note : _this.result_state_id2des_dict[2],
        // result_type_note : _this.result_type_id2des_dict[1],
        bg_color: item.bg_color,
        txt_color: item.txt_color,
        valType: '枚举',
      });
      this.markText2();
    },
  },
};
